import Vue from 'vue';
import Material from './material.vue'; // import axios from 'axios'

var MaterialBox = Vue.extend(Material);

Material.install = function (data) {
  var instance = new MaterialBox({
    data: data
  }).$mount(); // document.body.appendChild(instance.$el)

  instance.entrance = data.entrance || '';
  instance.user_id = data.user_id || '';
  instance.typeIndex = data.typeIndex || 0;
  Vue.nextTick(function () {
    instance.axios.get("/User_images/find_images_self").then(function (data) {
      console.log(data.data);
      instance.material_own_origin = data.data.dataImage;
      instance.material_own_cutout = data.data.dataCutout;

      if (data.data.dataImage.length != 0 || data.data.dataCutout.length != 0) {
        instance.loading = true;
      } else {
        instance.loading = false;
      }
    }).catch(function (e) {});
    instance.materialVisible = true; // show 和弹窗组件里的show对应，用于控制显隐
  });
};

export default Material;