import Vue from 'vue'
import axios from 'axios'

//将axios挂载在Vue扩展上
 Vue.prototype.axios=axios
//在其他地方使用只需使用 this.$http来代替axios;

 //配置baseUrl
 // axios.defaults.baseURL = '/api'

axios.interceptors.request.use(function (config) {
    var author_id = Vue.prototype.getCookie('author_id');
    // console.log(config)


    if(config.method == 'post' && config.headers.my_flag == undefined) {
        config.url = 'http://ht.idealead.hbindex.com/webapi' + config.url     
    }else if(config.method == 'get'){
        if(config['url'].indexOf('my_flag') == -1){
            config.url = 'http://ht.idealead.hbindex.com/webapi' + config.url   
        }
    }
    
    // 在发送请求之前做些什么
    if(author_id != '' && author_id != null){
        // console.log(config)
        if(config.method == 'post' && config.headers.my_flag == undefined) {
            config.data = {
                ...config.data,
                user_id: author_id,
            }
           
        }else if(config.method=='get' && config['url'].indexOf('my_flag') == -1) {
            config.params = { 
                user_id: author_id, 
                ...config.params 
            } 
        }
    }
    return config

}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});
