import bus from "@/bus.js";
export default {
  data: function data() {
    return {
      materialVisible: false,
      loading: false,
      loading_count: 0,
      entrance: '',
      user_id: '',
      typeIndex: Number,
      material_own_origin: [],
      //个人上传素材
      material_own_cutout: [],
      //个人上传素材的抠图
      picData: {
        upload_file_once: {}
      }
    };
  },
  computed: {
    material_own: function material_own() {
      return [].concat(_toConsumableArray(this.material_own_origin), _toConsumableArray(this.material_own_cutout));
    }
  },
  methods: {
    closedFunc: function closedFunc() {
      var material = document.getElementById('material');
      document.body.removeChild(material);
    },
    cout_load_img: function cout_load_img() {
      // 全部load完解除loading
      this.loading_count += 1;

      if (this.loading_count >= this.material_own.length) {
        this.loading = false;
      }
    },
    beforeUpload: function beforeUpload(file) {
      console.log(file); // 上传前

      var isFormat = file.type === 'image/jpeg' || file.type === 'image/png';
      var isLt2M = file.size / 1024 / 1024 < 2;
      this.$set(this.picData, 'upload_file_once', file);

      if (!isFormat) {
        this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
      }

      if (isFormat && !isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }

      return isFormat && isLt2M;
    },
    uploadSuccess: function uploadSuccess(res, file) {
      // 本地上传
      // 上传成功
      console.log(res);
      this.judgeUploadFrom(res);
    },
    uploadFail: function uploadFail(err, file, fileList) {
      // 上传失败
      this.$message(err);
    },
    selectPic: function selectPic(path, id) {
      // 从图库中选择
      this.judgeUploadFrom({
        data: {
          'file_id': id,
          'savepath': path
        }
      });
    },
    // 本地上传和图库选择 公共执行方法
    judgeUploadFrom: function judgeUploadFrom(res) {
      console.log(this.entrance);

      if (this.entrance == 'cutout') {
        bus.$emit('uploadSuccess_cutout', res);
      } else if (this.entrance == 'inputMsg') {
        if (this.typeIndex === 1) {
          // 上传logo成功
          bus.$emit('uploadSuccess_logo', res);
        } else if (this.typeIndex === 2) {
          // 上传logo成功
          bus.$emit('uploadSuccess_product', res);
        } else {
          this.$notify({
            title: '警告',
            message: '好像哪里不对哦！',
            type: 'warning'
          });
        }
      }

      this.materialVisible = false;
    }
  }
};