// import axios from 'axios';
export default {
  data: function data() {
    return {
      dialogVisible: false,
      loading: false,
      user_account: '',
      user_password: ''
    };
  },
  methods: {
    closedFunc: function closedFunc() {
      var login = document.getElementById('login');
      document.body.removeChild(login);
    },
    // 登录
    loginFunc: function loginFunc() {
      var me = this;

      if (me.checkAccountPass()) {
        me.loading = true;
        me.sendLoginRequest();
      } else {
        this.$notify({
          title: '提示',
          message: '账号或者密码不能为空！',
          type: 'warning'
        });
      }
    },
    checkAccountPass: function checkAccountPass() {
      // 检查账号密码是否为空
      if (this.user_account != '' && this.user_password != '') {
        return true;
      } else {
        return false;
      }
    },
    sendLoginRequest: function sendLoginRequest() {
      var me = this;
      me.axios.post("/User_login/user_login", {
        email: me.user_account,
        password: me.user_password
      }).then(function (data) {
        console.log(data);
        me.loading = false;
        var login_code = data.data.code,
            login_msg = data.data.message,
            login_data = data.data.data;

        if (login_code == 200) {
          // 登录成功 存数据
          me.saveAccount(login_data);
          me.loginSuccess(login_data);
          me.dialogVisible = false;
        } else {
          // 登录失败 
          me.$notify({
            title: '提示',
            message: login_msg,
            type: 'warning'
          });
        }
      }).catch(function (e) {
        console.log(e);
      });
    },
    saveAccount: function saveAccount(data) {
      // 保存数据
      var currentUser = JSON.stringify({
        'is_leader': data.is_leader,
        'user_name': data.user_name
      });
      this.setCookie('currentUser', currentUser);
      this.setCookie('isLogin', true);
      this.setCookie('author_id', data.id);
      this.setCookie('department_id', data.dep_id);
      this.setCookie('department_name', data.dep_name);
    }
  }
};