import '@babel/polyfill';
import Es6Promise from 'es6-promise';
import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import axios from 'axios';
import upperFirst from 'lodash/upperFirst';
import camelCase from 'lodash/camelCase';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '@/font/SiYuan.css';
import mixin from './mixin/mixin.js';
import '@/cookie.js';
import '@/axios.js';
import Material from './components/dialog/material';
import Login from './components/dialog/login';
Es6Promise.polyfill();
Vue.prototype.$material = Material.install;
Vue.prototype.$login = Login.install;
Vue.mixin(mixin);
Vue.use(ElementUI);
Vue.config.productionTip = false;

var requireComponent = require.context( // 其组件目录的相对路径
'./components', // 是否查询其子目录
false, // 匹配基础组件文件名的正则表达式
/\.vue$/);

requireComponent.keys().forEach(function (fileName) {
  // 获取组件配置
  var componentConfig = requireComponent(fileName); // 获取组件的 PascalCase 命名

  var componentName = upperFirst(camelCase( // 获取和目录深度无关的文件名
  fileName.split('/').pop().replace(/\.\w+$/, ''))); // 全局注册组件

  Vue.component(componentName, // 如果这个组件选项是通过 `export default` 导出的，
  // 那么就会优先使用 `.default`，
  // 否则回退到使用模块的根。
  componentConfig.default || componentConfig);
});
new Vue({
  router: router,
  store: store,
  render: function render(h) {
    return h(App);
  }
}).$mount('#app');